"use client";

import { useState } from "react";
import { Send, MessageCircle } from "lucide-react";

interface User {
  id: number;
  name: string;
  email: string;
  company: { name: string };
}

interface AddCommentsProps {
  users: User[];
  onAddComment: (comment: {
    user: User;
    text: string;
  }) => void;
}

export default function AddComments({ users, onAddComment }: AddCommentsProps) {
  const [selectedUser, setSelectedUser] = useState<User | null>(null);
  const [comment, setComment] = useState("");

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedUser || !comment.trim()) return;

    onAddComment({
      user: selectedUser,
      text: comment.trim(),
    });

    setComment(""); // reset
  };

  return (
    <div className="rounded-lg border text-card-foreground shadow-sm p-6 bg-white border-gray-200">
      {/* Header */}
      <div className="flex items-center gap-2 mb-4">
        <MessageCircle className="w-5 h-5 text-blue-600" />
        <h3 className="text-lg font-semibold text-foreground">Add a Comment</h3>
      </div>

      {/* Form */}
      <form className="space-y-4" onSubmit={handleSubmit}>
        {/* User Select */}
        <div>
          <label className="block text-sm font-medium text-foreground mb-2">
            Select User
          </label>
          <select
            value={selectedUser?.id || ""}
            onChange={(e) =>
              setSelectedUser(
                users.find((u) => u.id === Number(e.target.value)) || null
              )
            }
            className="w-full h-10 rounded-md border border-gray-300 bg-white px-3 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="">Choose a user...</option>
            {users.map((user) => (
              <option key={user.id} value={user.id}>
                {user.name} ({user.company.name})
              </option>
            ))}
          </select>
        </div>

        {/* Info about selected user */}
        {selectedUser && (
          <p className="text-sm text-gray-600">
            Commenting as{" "}
            <span className="font-medium">{selectedUser.name}</span> from{" "}
            {selectedUser.company.name}
          </p>
        )}

        {/* Comment Input */}
        <div>
          <label className="block text-sm font-medium text-foreground mb-2">
            Your Comment
          </label>
          <textarea
            value={comment}
            onChange={(e) => setComment(e.target.value)}
            className="flex min-h-[80px] w-full rounded-md border border-blue-400 bg-gray-50 px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 resize-none"
            placeholder="Write your comment here..."
            rows={4}
          />
        </div>

        {/* Submit Button */}
        <button
          type="submit"
          disabled={!selectedUser || !comment.trim()}
          className="flex items-center gap-2 bg-blue-600 hover:bg-blue-700 text-white font-medium px-4 py-2 rounded-md transition-colors disabled:opacity-50"
        >
          <Send size={16} />
          Post Comment
        </button>
      </form>
    </div>
  );
}
